








body{
  font-family: 'Microsoft JhengHei', '微軟正黑體', 'Microsoft YaHei', '微软雅黑', Tahoma, Verdana, sans-serif;
  font-size: 14px;
}

a{
  text-decoration: none;
  outline: none;
  color: #2B2B2B;
}

#main{
  width: 50%;
  margin: 0 auto;
}

@media screen and (max-width: 700px) {
  #main{
    width: 100%;
  }
}
@media screen and (max-width: 1000px) {
  #main{
    width: 90%;
  }
}



