














#header {
  z-index: 2;
  top: 0;
  right: 0;
  left: 0;
  box-shadow: 0 0 1px rgba(0,0,0,.15);
}

#header-warp {
  line-height: 65px;
  height: 65px;
}

#logo,#nav {
  height: 100%;
}

#logo {
  display: inline-block;
  float: left;
}

#logo>a {
  font-size: 22px;
  color: #444;
}

#nav {
  float: right;
}

#nav a {
  font-size: 14px;
  margin-left: 30px;
}



